import React from "react";
import comics from "../data/comics.json";
import {Image} from "react-bootstrap"
import Y from "../images/Y.jpg"
class comicList extends React.Component{
constructor(props){
  super(props);
}

 selectNewComic = (comic) => {
    console.log("comic selected", comic);
    this.props.history.push("/details/" + comic.id)
  };
  render(){
  return(
     <div className="container " style={{backgroundImage:`url(${Y})`,backgroundRepeat:"no-repeat",minWidth:"100%",backgroundSize:"cover"}}>
      
  <h7 className=" font-weight-bolder  fluid ml-4" style={{fontSize:"60px",Color:"white" }}lg={12}><a href="/comic">Comics Books</a></h7>
  <div class="row mb-5" >
  
    {comics.map((comic, index) =>{
      return<div key={index} className="col-lg-3 col-lg-3 col-lg-3 bg-primary ml-5" style={{justifyContent:"space-round" ,border:"1px white solid"}}>
         

        <h2 className="Title" style={{cursor:"pointer"}} onClick={() => this.selectNewComic(comic)}>{comic.title}</h2><div><Image  src={comic.imageUrl}  className="mb-3" style={{cursor:"pointer"}} onClick={() => this.selectNewComic(comic)}/></div></div>
      
    })}
  </div>
  </div>
  )
}};

export default comicList