import React from 'react';
import "./App.css";
import Navbar from "./components/navbar";
// import Message from "./components/message";
import Login from "./components/login";
import Content from "./components/allContents"
import Comics from "./components/comicList"
import ComicDetails from "./components/comicDetails"
import Footer from "./components/footer"
import {BrowserRouter as Router,Route,Switch} from "react-router-dom";
import Post from "./components/postProfile"
import Profile from "./components/index"
import axios from "axios"
import GoogleLogin from "react-google-login"
// import axios from "axios"
class App extends React.Component {
state = {
}
 componentDidMount= async()=>{
  await  axios.get("/users/login")
   .then(res=>{this.setUser(res.data)})
   .catch(err=>{console.log(err)})
} 
setUser= user =>{
this.setState({user:user})
}
responseGoogle (response){
  console.log(response)
  console.log(response.profileObj)
}
render() {
  
  return (
    <div className="App" >

  
    
  <Router>
  <Navbar user={this.state.user} logout={() => this.setUser(undefined)}/>
    <Switch>
      <Route path="/login" exact component={()=><Login setUser={this.setUser}/>}/>
      <Route path="/home" component={()=><Content user={this.state.user}/>}/>
      <Route path="/comic"  component={Comics}/>
      {/* <Route path="/message" component={Message}/> */}
      <Route path="/profile/post" component={Post}/>
      <Route path="/details/:id" exact component={ComicDetails} />
      <Route path="/profile" component={Profile}/>
       
      </Switch>
  </Router>
 <Footer/>
    </div>
  
  );
  }  
}
export default App;
