import React from "react"
import {Form,Button,Spinner,Alert,Modal} from "react-bootstrap"
import {Redirect} from "react-router-dom"
import "../App.css"
import axios from "axios"
import JK from "../images/JK.jpg"
import index from "../index.js"
import {withRouter} from "react-router-dom" 

class LoginForm extends React.Component {
constructor(props){
 super(props);
  this.state= {
    email:"",
    password:"" 
  }
  this.login=this.login.bind(this)
  this.onChange=this.onChange.bind(this)
}
 
onChange=(e)=>{
this.setState({[e.target.name]:e.target.value})
}
 componentDidMount =()=>{
 
 axios.get("/users/login")
 .then(res=>
   this.setState({user:res.data}))
 .catch(err=>console.log(err))
 }
 login(){  
const data ={email:this.state.email,password:this.state.password}   
axios.post("/users/login",data)
.then(res =>
  {
    localStorage.setItem('token', res.data.token)
    this.setState({loggedIn:true})
    console.log(res.data)
    this.props.setUser(res.data);
    this.props.history.push("/home")
  })
.catch((err) => console.log(err))

}
onSubmit (e) {
  e.preventDefault();
  }

render(){
const {email,password}=this.state
// if(this.state.loggedIn){
//   return (<Redirect to={"/home"}/>)
// }
return(<div className="container" style={{backgroundImage:`url(${JK})`,position:"fixed",minWidth:"100%"}}>
  <>
<Form className="Form bg-dark mt-5" onSubmit={this.onSubmit}> 
  <Form.Group controlId="formBasicEmail">
    <Form.Label >Email</Form.Label>
    <Form.Control className="Email" type="email" name="email" placeholder="Enter email" 
      value={this.state.email} 
     onChange={this.onChange} />
    <Form.Text className="text-muted">
      We'll never share your email with anyone else.
    </Form.Text>
  </Form.Group>

  <Form.Group controlId="formBasicPassword">
    <Form.Label >Password</Form.Label>
    <Form.Control className="Password"  type="password" name="password" placeholder="Password" value={this.state.password} onChange={this.onChange}/>
  </Form.Group>
  <Form.Group controlId="formBasicCheckbox">
    <Form.Check type="checkbox" label="Check me out" />
  </Form.Group>
 <Button variant="dark" type="submit"   onClick={this.login}>
 Login
  </Button>
</Form>
</>
</div>
)
}}
export default  withRouter(LoginForm)